type TrackParmsType = {
  bizType: string
  pageLevelId: string
  clickId?: string
  logType?: string
  pageParam?: any
  positionId?: string
  positionParam?: any
}

/* / */
export const trackClick = (options: TrackParmsType) => {
  const {
    bizType,
    pageLevelId,
    clickId,
    positionId,
    logType = '20000'
  } = options
  let { pageParam, positionParam } = options
  pageParam = pageParam ? JSON.stringify(pageParam) : undefined
  positionParam = positionParam ? JSON.stringify(positionParam) : undefined
  try {
    const params = {
      bizType, // 
      pageLevelId, //  ID
      clickId, // 
      logType, // : '10000''20000'
      pageParam, // 
      positionId, // 
      positionParam // 
    }
    console.log(params)
    window.track && window.track._launch(params)
  } catch (e) {
    console.error(e)
  }
}

/*  */
export const trackView = (options: TrackParmsType) => {
  const { bizType, pageLevelId, positionId, pageParam, positionParam } = options
  const params = {
    bizType,
    pageLevelId,
    logType: '10000',
    pageParam,
    positionId,
    positionParam
  }
  trackClick(params)
}
